import Link from "@material-ui/core/Link";
import { createUseClassNames } from "theme/useClassesNames";
import Typography from "@material-ui/core/Typography";

type Item = {
    name: string;
    url: string;
    /** 
     * The svg must not have fill color or the dark mode won't distinguish 
     * Import your svg like that: import { ReactComponent as RedditSvg } from "assets/svg/reddit.svg";
     * and pass RedditSvg as logoSvgComponent.
     * */
    logoSvgComponent?: (props: {})=> ReturnType<React.FC>;
};

export type Props = {
    leftItems: Item[];
    rightItems: Item[];
    licence: string;
}

const { useClassNames } = createUseClassNames()(
    (theme) => ({
        "root": {
            "display": "flex",
            "flexDirection": "column",
            "alignItems": "center",
            "color": theme.palette.type === "dark" ? theme.custom.silverGray : "black",
            "& a": {
                "color": theme.palette.type === "dark" ? theme.custom.silverGray : "black"
            },
            "backgroundColor": theme.palette.type === "dark" ? theme.custom.backgroundDark : theme.custom.backgroundLight,
            "marginTop": 80,
            "@media (max-width: 895px)": {
                "marginTop": 0
            }
        },
        "socialMedia": {
            "margin": 40,
            "& svg": {
                "fill": theme.palette.type === "dark" ? theme.custom.silverGray : "black",
                "marginRight": 20,
                "height": 40,
                "width": 40,
            },
            "& a": {
                "display": "flex",
                "alignItems": "center",
                "height": 60,
            },
            "@media (max-width: 440px)": {
                "margin": "40px 10px 40px 10px"
            }

        },
        "licence": {
            "fontStyle": "italic"
        },
        "wrapper": {
            "display": "flex"
        }
    })
)


export const Footer = (props: Props) => {

    const { licence, leftItems, rightItems } = props;

    const { classNames } = useClassNames({});

    return (
        <footer className={classNames.root}>
            <div className={classNames.wrapper}>
                {([leftItems, rightItems] as const).map((items, i) =>
                    <div className={classNames.socialMedia} key={i}>
                        {
                            items.map(({ url, name, logoSvgComponent: LogoSvg }) =>
                                <Link href={url} key={url + name} >
                                    {LogoSvg && <LogoSvg/>}
                                    <Typography>
                                        {name}
                                    </Typography>
                                </Link>
                            )
                        }
                    </div>
                )}
            </div>

            <p className={classNames.licence}>
                {licence}
            </p>

        </footer>
    )
}